import {Component} from '@angular/core';
import {NavController, NavParams, ToastController} from 'ionic-angular';
import {ApplicationRecordService} from "../../providers/application-record.service";
import {ListviewService} from "../../providers/listview.service";
import _ from 'lodash';

/**
 * Generated class for the ApplicationRecordPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage(
//   {
//     name:'application-record',
//     segment:'applicationRecord',
//   }
//   )
@Component({
  selector: 'page-application-record',
  templateUrl: 'application-record.html',
})
export class ApplicationRecordPage {


  pagesCtrl = {

    isLastPage: false,
    pageNo: 0,
    pageSize: 20,

  };
  recordList = [];


  constructor(public navCtrl: NavController,
              public aRecordService: ApplicationRecordService,
              public toastCtrl: ToastController,
              public listviewService: ListviewService,
              public navParams: NavParams) {
    for (let i = 0; i < 30; i++) {
      this.recordList.push(
        {
          status: '预审批中' + i,
          applyStatus: 0,
          applyTime: '2017/09/09 15:30',
          customerName: '薛琰',
          customerId: '1',
          money: '5,000.00',
          tel: '13733338242',
          repaymentTimes: '6个月' + i,
          btn: Math.random() > 0.5 ? {
            text: '补充资料',
            key: 'zhiliao',
          } : {
            text: '确认预约',
            key: 'appointment',
          },
        }
      );
    }


  }

  ionViewDidLoad() {
    this.renderLoansList();
  }


  doRefresh(refresher) {
    this.pagesCtrl = this.listviewService.resetPagesCtrl();
    this.renderLoansList(refresher,true);
  }

  doInfinite(infiniteScroll) {
    console.log('Begin async operation');
    this.renderLoansList(infiniteScroll);
  }

  renderLoansList(fn?,emptyList=false) {
    this.aRecordService.fetchLoansList({pageNo: 0, pageSize: 20}).subscribe((res) => {
      if (res.code === 0 && res.success) {
        if (res.data && Array.isArray(res.data.list)) {
          // 搜索的情况下或者下拉刷新的情况下，情况列表
          if(emptyList){
            this.recordList = [];
          }
          this.recordList = this.recordList.concat(res.data.list);
        }
        this.pagesCtrl.isLastPage = !res.data.isLastPage;
        // 如果不是最后一页的话，页数加一
        if (res.data.isLastPage) {
          this.pagesCtrl.pageNo++;
        }
        if (fn&&_.isFunction(fn['complete'])) {
          fn.complete();
        }
      } else {
        let toast = this.toastCtrl.create({
          message: res.meesage,
          duration: 1000
        });
        toast.present();
      }
    }, () => {

    })
  }

  // 操作用户进入补充资料页面还是进入确认预约页面
  operate(type) {
    if (type === 'zhiliao') {
      console.log('补充资料');
      // 完善信息页面进入
      this.navCtrl.push('informationIndex');
    } else if (type === 'appointment') {
      console.log('确认预约');
      this.navCtrl.push('appointment');
    }
  }

  // 搜索操作
  handleInput(val) {
    console.log('val', val);
    this.pagesCtrl = this.listviewService.resetPagesCtrl();
    this.renderLoansList({},true);
  }


}



