import {Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';
import {Observable} from "rxjs/Observable";

/**
 * Generated class for the SearchLineComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'search-line',
  templateUrl: 'search-line.html'
})
export class SearchLineComponent implements OnInit{

  @ViewChild('searchLine') searchLine:ElementRef;
  @Output() handleClick = new EventEmitter();
  @Input() searchIndexList = [
    {text: 'A', key: 'A'},
    {text: 'B', key: 'B'},
    {text: 'C', key: 'C'},
    {text: 'D', key: 'D'},
    {text: 'E', key: 'E'},
    {text: 'F', key: 'F'},
    {text: 'G', key: 'G'},
    {text: 'H', key: 'H'},
    {text: 'I', key: 'I'},
    {text: 'J', key: 'J'},
    {text: 'K', key: 'K'},
    {text: 'L', key: 'L'},
    {text: 'M', key: 'M'},
    {text: 'N', key: 'N'},
    {text: 'O', key: 'O'},
    {text: 'P', key: 'P'},
    {text: 'S', key: 'S'},
    {text: 'R', key: 'R'},
    {text: 'T', key: 'T'},
    {text: 'U', key: 'U'},
    {text: 'V', key: 'V'},
    {text: 'W', key: 'W'},
    {text: 'X', key: 'X'},
    {text: 'Y', key: 'Y'},
    {text: 'Z', key: 'Z'},
    {text: '#', key: '#'},
  ];

  constructor() {
    console.log('Hello SearchLineComponent Component');
  }
  ngOnInit(): void {
    console.log('searchLine',this.searchLine);
    console.log('searchLine',this.searchLine.nativeElement);
    const pEl = this.searchLine.nativeElement.querySelector('p');

  }

  handleSearchClick(item){
    this.handleClick.emit(item)
  }

}



