import {LoginPage} from "../pages/login/login";
import {TabsPage} from "../pages/tabs/tabs";
import {ApplicationRecordPage} from "../pages/application-record/application-record";
import {InformationPage} from "../pages/application-record/information/information";
import {AppointmentPage} from "../pages/application-record/appointment/appointment";
import {IdentityInfoPage} from "../pages/application-record/identity-info/identity-info";
import {UploadInfoPage} from "../pages/application-record/upload-info/upload-info";
import {WorkInfoPage} from "../pages/application-record/work-info/work-info";
import {AppointmentCustomerPage} from "../pages/my/appointment-customer/appointment-customer";
import {MyPage} from "../pages/my/my";
import {MyDetailPage} from "../pages/my/my-detail/my-detail";
import {MyCustomerPage} from "../pages/my/my-customer/my-customer";
import {LoanRecordPage} from "../pages/my/loan-record/loan-record";
import {MyMessagePage} from "../pages/my/my-message/my-message";
import {MyMessageDetailPage} from "../pages/my/my-message-detail/my-message-detail";
import {EditPwdPage} from "../pages/my/edit-pwd/edit-pwd";
import {EditTelFirstPage} from "../pages/my/edit-tel-first/edit-tel-first";
import {EditTelSecondPage} from "../pages/my/edit-tel-second/edit-tel-second";
import {AssociationsCustomersPage} from "../pages/associations-customers/associations-customers";
import {ForgetPwdPage} from "../pages/login/forgetPwd/forgetPwd";
import {LoginAuthPage} from "../pages/login/loginAuth/loginAuth";
import {MessageAuthPage} from "../pages/login/messageAuth/messageAuth";
import {ResetPwdPage} from "../pages/login/resetPwd/resetPwd";

export default [
  /*登录模块 路由*/
  {component: LoginPage, name: 'login', segment: 'login'},
  {component: ForgetPwdPage, name: 'forgetPwd', segment: 'forgetPwd', defaultHistory: ['login']},
  {component: LoginAuthPage, name: 'loginAuth', segment: 'loginAuth', defaultHistory: ['login']},
  {component: MessageAuthPage, name: 'messageAuth', segment: 'messageAuth', defaultHistory: ['login']},
  {component: ResetPwdPage, name: 'reset', segment: 'reset', defaultHistory: ['login']},

  /* tabs 路由*/
  {component: TabsPage, name: 'Home', segment: 'home', loadChildren: 'record'},

  /*申请记录模块 路由*/
  {component: ApplicationRecordPage, name: 'record', segment: 'record', loadChildren: 'informationIndex'},
  {component: InformationPage, name: 'informationIndex', segment: 'informationIndex', defaultHistory: ['record']},
  {component: AppointmentPage, name: 'appointment', segment: 'appointment', defaultHistory: ['record']},

  {component: IdentityInfoPage, name: 'identity-info', segment: 'identity-info', defaultHistory: ['record','informationIndex']},
  {component: UploadInfoPage, name: 'upload-info-index', segment: 'upload-info-index', defaultHistory: ['record','informationIndex']},
  {component: WorkInfoPage, name: 'work-info', segment: 'work-info', defaultHistory: ['record','informationIndex']},

  /*关联客户 路由*/
  {component:AssociationsCustomersPage,name: 'associations-customers', segment: 'associations-customers'},

  /*我的模块 路由*/
  {component: MyPage, name: 'my', segment: 'my', loadChildren: 'appointment-customer'},
  {component: AppointmentCustomerPage, name: 'appointment-customer', segment: 'appointment-customer', defaultHistory:['my']},
  {component: MyDetailPage, name: 'my-detail', segment: 'my-detail', defaultHistory:['my']},
  {component: MyCustomerPage, name: 'my-customer', segment: 'my-customer', defaultHistory:['my']},
  {component: LoanRecordPage, name: 'loan-record', segment: 'loan-record', defaultHistory:['my']},
  {component: MyMessagePage, name: 'my-message', segment: 'my-message', defaultHistory:['my']},
  {component: MyMessageDetailPage, name: 'my-message-detail', segment: 'message-detail/:id', defaultHistory:['my','my-message']},
  {component: EditPwdPage, name: 'editPwd', segment: 'editPwd', defaultHistory:['my','editTelFirst']},
  {component: EditTelFirstPage, name: 'editTelFirst', segment: 'editTelFirst', defaultHistory:['my']},
  {component: EditTelSecondPage, name: 'edit-tel-second', segment: 'edit-tel-second', defaultHistory:['my','editTelFirst']},

]



