import { createSelector } from '@ngrx/store';
import {IAppState,IUserDataState} from './interface';
/** Initial State */
const initialState = {
    isFetching: false,
    userData:{
      token:'',
    }
};



export const selectUserData = (state: IAppState) => state.user.userData;
export const selectToken = createSelector(selectUserData, (state: IUserDataState) => state.token);

const REQUEST: string = 'user/REQUEST';
const USER_LOGGED_IN_SUCCESS: string = 'user/USER_LOGGED_IN_SUCCESS';
const USER_LOGGED_OUT_SUCCESS: string = 'user/USER_LOGGED_OUT_SUCCESS';

export   function userReducer(state: any = initialState, {type, payload}) {
  switch (type) {
    case REQUEST:
      return Object.assign({}, state, {
        isFetching: true,
      });
    case USER_LOGGED_IN_SUCCESS:
      return {
        isFetching: false,
        userData:{
          token:payload.token,
        }
      };
    case USER_LOGGED_OUT_SUCCESS:
      return {
        isFetching: false,
        userData:{
          token:'',
        }
      };
    default:
      return state;
  }

}
export  function userLoggedInSuccess(user){
  return {
    type: USER_LOGGED_IN_SUCCESS,
    payload: {
      salesmanToken:user.salesmanToken,
    },
  };
}
export function userLoggedOutSuccess(message?: any){
  return {
    type: USER_LOGGED_OUT_SUCCESS,
    payload: {
      message,
    },
  };
}



