import {Component, ViewChild} from '@angular/core';
import {AlertController, Content,  NavController, NavParams} from 'ionic-angular';
import {MyCustomerService} from "./my-customer.service";

/**
 * Generated class for the MyCustomerPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
//
// @IonicPage({
//   name: 'my-customer'
// })
@Component({
  selector: 'page-my-customer',
  templateUrl: 'my-customer.html',
})
export class MyCustomerPage {
  testData = [
    {name: '陈少敏', tel: '13533320344'},
    {name: '陈晓', tel: '15533320344'},
    {name: '春节后', tel: '16633320344'},
    {name: '狄仁杰', tel: '13533320344'},
    {name: '东西', tel: '13533324444'},
    {name: '东乡县', tel: '14433320344'},
    {name: '德里克', tel: '13533320344'},
    {name: '火少敏', tel: '13533320344'},
    {name: '火少敏', tel: '13533320344'},
    {name: '火少敏', tel: '13533323333'},
    {name: '吴晓敏', tel: '33333320344'},
    {name: '王小明', tel: '13533320344'},
    {name: '吴小华', tel: '13533323333'},
    {name: '文学奖', tel: '13533320344'},
    {name: '王小明', tel: '13533320344'},
    {name: '#', tel: '13533320344'},
    {name: '#', tel: '13533320344'},
    {name: '#', tel: '13533323333'},
    {name: '#', tel: '13533320344'},
  ];
  contactsList = [];
  searchIndexList =[];
  @ViewChild(Content) content: Content;

  constructor(public navCtrl: NavController,
              private alertCtrl: AlertController,
              private myCustomerService: MyCustomerService,
              public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MyCustomerPage');
    this.contactsList = this.myCustomerService.getContactList(this.testData);
    this.searchIndexList =this.contactsList.map(val=>{return {text:val.title,key:val.title}})
  }

  handleInput(val) {
    console.log('val', val);
    let data = this.myCustomerService.filterContactList(this.testData,val.value);
    this.contactsList = this.myCustomerService.getContactList(data);
  }

  handleClick(item) {
    console.log('item', item);
    const that = this;

    const elList = Array.prototype.slice.call(document.querySelectorAll('.listTitle[data-key]'));
    console.log('elList', elList);
    elList.forEach(val => {
      //
      //   console.log('val',);
      if (val.dataset['key'] === item['key']) {
        console.log('val', val);
        console.log('offsetTop', val.offsetTop);
        that.content.scrollTo(0, val.offsetTop, 300);
      }
    })

  }

  tel(nameVal, telVal) {
    let alert = this.alertCtrl.create({
      cssClass: 'contactAlert',
      title: '联系Ta',
      message: `<p>您将致电</p><p>客户：${nameVal} <span>${telVal}</span></p>`,
      buttons: [{
        text: '取消',
        role: 'cancel',
        handler: () => {
          console.log('Cancel clicked');
        }
      },
        {
          text: '确定',
          handler: () => {
            window.location.href =`tel:${telVal}` ;
          }
        }]
    });
    alert.present();
  }
}



