import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import {Observable} from 'rxjs/Observable';
import {SERVER,SERVERMAP} from '../config';
import _ from 'lodash';
import { Store, select } from '@ngrx/store';
import { selectToken } from '../stores/user.store';
import {AlertController, ModalController, ToastController} from 'ionic-angular';
import { ErrorPage } from '../pages/error/error'

export   interface IParams {
    host?:string
    headers?:any,//fetch header
    showError?:number, //重写错误处理；不填默认使用弹窗msg;
    preRedirectHandle?(error):void;
    preErrorHandle?(error):void;
    ignore?:boolean, //该参数为true，那么即使data.status==FAILED也会被解析到promise的resolve里面
    timeout?:number, //秒！不是ms
    // dataAdapter?:'none'|'any'|'customer'|Function|{
    //   use:string,
    //   next(v):IDataAdapter
    // }
}
export interface IError{
    errNo:number, //-1请求没出门，直接GG;-2服务器返回非200状态;-3服务器正确返回错误（wtf?）;-4请求超时
    status?:any,//就是http status
    msg?:string,//错误信息
    code?:string, //错误信息
    data?:any
}
@Injectable()
export class HttpService {

    private salesmanToken:string;
    private defaultSettings={
        host:SERVER.MAIN,
        headers:{
            'Accept': 'application/json',
            "Content-Type": "application/json"
        },
        timeout:60,
        showError:1
    };
    constructor(private http: HttpClient,
                private store:Store<any>,
                private modalCtrl:ModalController ,
                private alertCtrl: AlertController,
                private toastCtrl:ToastController) {
        this.store.pipe(select(selectToken)).subscribe((salesmanToken)=>{
            this.salesmanToken=salesmanToken;
        });
    }
    private request(type,url,req,exParams){
        let params:IParams=_.merge({},this.defaultSettings,exParams);

        if(this.salesmanToken){
            params.headers.Authorization=this.salesmanToken;
        }
        let exHost=(exParams&&exParams.host)||'';
        if(!url.match(/:[/]{2,3}/)){
            //host 的取值优先级->exHost(调用接口传的参数)->SERVERMAP（配置）->base（MAIN字段）
            let host=params.host;
            //console.log(url.match(/[?].*/))
            let path=url.replace(/[?].*/,'');
            if(SERVERMAP[path]){
                host=SERVERMAP[path];
            }
            if(exHost){
                host=exHost;
            }
            url=host+url;
        }
        console.log(url);
        console.log(params);
        let request$:Observable<any>
        if(type=="GET"){
            console.log('~~~~~~~~~~~~~~~~~~~')
            request$=this.http.get(url,{
                headers:params.headers,
                params:req
            })
        }
        if(type=="POST"){
            request$=this.http.post(url,req,{
                headers:params.headers
            })
        }
        return request$
        .timeout(params.timeout*1000)
        .map((response)=>{
            if(response.success||params.ignore){
                return response.data
            }else{
              // 001001	用户没有登陆或者登陆已过期
              if(response.code =='001001'){
                let alert = this.alertCtrl.create({
                  title: '登录过期',
                  subTitle: '',
                  message:'登录过期，请重新登录',
                  buttons: [{
                    text:'确定',
                    handler: data => {
                      console.log('Cancel clicked');
                      console.log('window',window);
                    }
                  }]
                });
                alert.present();
              }
              // 20001	session会话过期。 见于通过验证码重设密码时会话过期，或者登录时重设密码会话过期
              if(response.code =='20001'){
                let alert = this.alertCtrl.create({
                  title: '验证码过期',
                  subTitle: '',
                  message:'验证码过期，请重新获取验证码',
                  buttons: [{
                    text:'确定',
                    handler: data => {
                      console.log('Cancel clicked');
                    }
                  }]
                });
                alert.present();
              }

              throw({
                    name:"ServerErrorResponse",
                    msg:response.msg,
                    data:response.data,
                    code:response.code
                })


            }

        }).catch((err)=>{
            console.log('err',err);
            let error:IError;
            if(!err.name){
                error=({errNo:-1,msg:`请求错误`})
            }else if(err.name=="HttpErrorResponse"){
                error=({errNo:-2,msg:`请求失败,状态码${err.status}`,status:err.status});
            }else if(err.name=="ServerErrorResponse"){
                error=({errNo:-3,msg:err.msg,data:err.data,code:err.code})
            }else {
                error=({errNo:-4,msg:"请求超时"});

            }
            if(params.showError){
                let preRedirectHandle;
                let preErrorHandle;
                if(params.showError==1){

                    preRedirectHandle=(error)=>{
                        // if(isNeedRedirect){
                        //     return true;
                        // }
                    };

                    preErrorHandle=(error)=>{
                        let toast = this.toastCtrl.create({
                            message: error.msg,
                            duration: 3000
                          });
                          toast.present();
                    }
                }else if(params.showError==2){
                    //不需要重定向
                    preRedirectHandle=(error)=>{};
                    //
                    preErrorHandle=(error)=>{
                        let errorModal=this.modalCtrl.create(ErrorPage,{error});
                        errorModal.present();
                    }
                }
                if(params.preRedirectHandle){
                    preRedirectHandle=params.preRedirectHandle;
                }
                if(params.preErrorHandle){
                    preErrorHandle=params.preErrorHandle;
                }
                preRedirectHandle(error);
                preErrorHandle(error);

            }

            throw(error);
        });
    }
    public post(url:string, body?:object, params?:IParams){
        return this.request("POST",url,body,params);
    }
    public get(url:string, body?:object, params?:IParams){
        return this.request("GET",url,body,params);
    }
}



