import {Injectable} from '@angular/core';
import {Observable} from 'rxjs/Observable';

declare var pinyinUtil: any;
import "rxjs/add/operator/map";


/*
  Generated class for the IdentityInfoProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class MyCustomerService {

  constructor() {
  }

  getContactList(data = []) {
    let saveAry = [
      {title: 'A', detailList: []},
      {title: 'B', detailList: []},
      {title: 'C', detailList: []},
      {title: 'D', detailList: []},
      {title: 'E', detailList: []},
      {title: 'F', detailList: []},
      {title: 'G', detailList: []},
      {title: 'H', detailList: []},
      {title: 'J', detailList: []},
      {title: 'K', detailList: []},
      {title: 'L', detailList: []},
      {title: 'Q', detailList: []},
      {title: 'W', detailList: []},
      {title: 'E', detailList: []},
      {title: 'R', detailList: []},
      {title: 'T', detailList: []},
      {title: 'Y', detailList: []},
      {title: 'U', detailList: []},
      {title: 'I', detailList: []},
      {title: 'O', detailList: []},
      {title: 'P', detailList: []},
      {title: 'Z', detailList: []},
      {title: 'X', detailList: []},
      {title: 'C', detailList: []},
      {title: 'V', detailList: []},
      {title: 'B', detailList: []},
      {title: 'N', detailList: []},
      {title: 'M', detailList: []},
      {title: '#', detailList: []}
    ];
    const reg = /^[A-Za-z]+$/;
    data.forEach(val => {
      const str = pinyinUtil.getFirstLetter(val.name).slice(0, 1);
      console.log('str', str);
      if (reg.test(str)) {
        saveAry.forEach(_val => {
          if (_val['title'] === str) {
            _val['detailList'].push({name: val.name, tel: val.tel})
          }
        })
      } else {
        saveAry.forEach(_val => {
          if (_val['title'] === '#') {
            _val['detailList'].push({name: val.name, tel: val.tel})
          }
        })
      }
    });
    console.log('saveAry', saveAry);
    let contactList = [...saveAry];

    return contactList.filter(val => {
      return val.detailList.length > 0
    });
  }

  filterContactList(data = [], searchKey) {
    if (searchKey.trim().length === 0) {
      return data;
    }
    const _data = [...data];
    return _data.filter(val => {
      console.log('val',val);
      return val['name'].indexOf(searchKey) > -1 || val['tel'].indexOf(searchKey) > -1
    })
  }

}



