import {Component} from '@angular/core';
import {NavController} from 'ionic-angular';
import {LoginAuthPage} from './loginAuth/loginAuth';
import {ForgetPwdPage} from './forgetPwd/forgetPwd';
import {UserService} from "../../providers/user.service";
import {Observable} from "rxjs/Observable";
import {Store,select} from "@ngrx/store";
import {selectToken} from "../../stores/user.store";


// @IonicPage(
//   {
//     name:'login',
//     segment:'login',
//   }
// )
@Component({
  selector: 'page-login',
  templateUrl: 'login.html'
})
export class LoginPage {
  pwdURL: string = 'assets/imgs/login/eyes.png';


  constructor(public navCtrl: NavController,
              public store:Store<any>,
              public userService:UserService) {
    //this.data$  = this.store.pipe(select('userData'))

    this.store.pipe(select(selectToken)).subscribe((token)=>{
      //this.token=token;
      //console.log('token',this.token);
    });
    //console.log('token',this.token);
  }

  // 切换显式展示密码
  switchPwd() {
    // console.log('pwdURL', this.pwdURL);
    this.pwdURL = this.pwdURL === 'assets/imgs/login/eyes.png' ? 'assets/imgs/login/eyesopen.png' : 'assets/imgs/login/eyes.png'
  }

  login() {
    console.log('navCtrl', this.navCtrl);
     this.navCtrl.push(LoginAuthPage);
    this.userService.login({useraccount:'ddd',password:'ddddd'}).subscribe((res)=>{
      console.log('res',res);
    })
  }

  forgetPwd() {
    this.navCtrl.push(ForgetPwdPage);
  }
}



