import {HTTP_INTERCEPTORS, HttpClientModule} from '@angular/common/http';
import {ErrorHandler, NgModule} from '@angular/core';
import {BrowserModule} from '@angular/platform-browser';
import {SplashScreen} from '@ionic-native/splash-screen';
import {StatusBar} from '@ionic-native/status-bar';
import {IonicStorageModule} from '@ionic/storage';
import {IonicApp, IonicErrorHandler, IonicModule} from 'ionic-angular';


import {HttpService} from '../providers/http.service'
import {UserService} from '../providers/user.service'
// reducer
import {StoreModule} from '@ngrx/store';

import {userReducer} from '../stores/user.store';


import {MyApp} from './app.component'
import {TabsPage} from "../pages/tabs/tabs";
import {ErrorPage} from "../pages/error/error";

import {ComponentsModule} from "../components/components.module";
import {ApplicationRecordPageModule} from "../pages/application-record/application-record.module";
import {MyPageModule} from "../pages/my/my.module";
import {AssociationsCustomersPageModule} from "../pages/associations-customers/associations-customers.module";
import {LoginModule} from "../pages/login/login.module";

import RouterConfig from './app.route.module';
import {HttpLoadingInterceptor} from "../providers/http-loading-interceptor";
import {ListviewService} from "../providers/listview.service";

@NgModule({
  declarations: [
    MyApp,
    TabsPage,
    ErrorPage
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    StoreModule.forRoot({user: userReducer}),
    IonicModule.forRoot(MyApp, {
      backButtonText: '返回',
      tabsHideOnSubPages: true,
      swipeBackEnabled: false,
      preloadModules: true,
      iconMode: 'ios',
      mode: 'ios'
    }, {
      links: RouterConfig
    }),
    IonicStorageModule.forRoot(),

    ComponentsModule,
    LoginModule,
    ApplicationRecordPageModule,
    MyPageModule,
    AssociationsCustomersPageModule,
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    ErrorPage,
    TabsPage

  ],
  providers: [
    StatusBar,
    SplashScreen,
    HttpService,
    UserService,
    ListviewService,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    {provide: HTTP_INTERCEPTORS, useClass: HttpLoadingInterceptor, multi: true},

  ]
})
export class AppModule {
}



