import {Component, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';
import {Observable} from "rxjs/Observable";
import "rxjs/add/operator/map";
import "rxjs/add/operator/debounceTime";
import "rxjs/add/operator/filter";

/**
 * Generated class for the SearchComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'search',
  templateUrl: 'search.html'
})
export class SearchComponent implements  OnInit{
  @ViewChild('searchInput') searchInput:any;
  @Input() placeText = '请输入姓名/电话号码';
  @Input() inputType = 'text';
  @Output() handleInput = new EventEmitter();
  showSearchMask:boolean = false;
  searchContent:string = '';

  constructor() {
    console.log('Hello SearchComponent Component');
    console.log('searchInput',this.searchInput);

  }

  ngOnInit(){
    console.log('searchInput',this.searchInput.nativeElement);
    //
    Observable.fromEvent(this.searchInput.nativeElement,'input').debounceTime(1000).filter(e=>{
      return e['target'].value.length>=0
    }).map((e)=>{
      console.log('e',e);
      return e['target'].value
    }).subscribe((val)=>{
      console.log(val);
      // ajax
      this.handleInput.emit({value:val});
      this.showSearchMask = false;
      this.searchInput.nativeElement.blur();
    })
  }
  focusSearch(){
    console.log('focus');
    this.showSearchMask = true;
  }

  delete(){
    this.searchContent = '';
  }

  cancel(){
    this.searchContent = '';
    this.showSearchMask = false;
  }


}



