import {NgModule} from '@angular/core';
import {IonicModule, IonicPageModule} from 'ionic-angular';

import {ComponentsModule} from "../../components/components.module";
import {LoginPage} from "./login";
import {ForgetPwdPage} from "./forgetPwd/forgetPwd";
import {LoginAuthPage} from "./loginAuth/loginAuth";
import {MessageAuthPage} from "./messageAuth/messageAuth";
import {ResetPwdPage} from "./resetPwd/resetPwd";
import {PipesModule} from "../../pipes/pipes.module";

@NgModule({
  declarations: [
    LoginPage,
    ForgetPwdPage,
    LoginAuthPage,
    MessageAuthPage,
    ResetPwdPage
  ],
  entryComponents: [
    ForgetPwdPage,
    LoginPage,
    LoginAuthPage,
    MessageAuthPage,
    ResetPwdPage
  ],
  exports: [ForgetPwdPage,
    LoginAuthPage,
    LoginPage,
    MessageAuthPage,
    ResetPwdPage],
  imports: [
    IonicModule,
    PipesModule,
    ComponentsModule
  ],
})
export class LoginModule {
}



