import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the MyDetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage({
//   name:'my-detail'
// })
@Component({
  selector: 'page-my-detail',
  templateUrl: 'my-detail.html',
})
export class MyDetailPage {

  myDetail={
    tel:'1399998732',
    account:'ffffffffffff',
    profileURL:'assets/imgs/application-record/profile.png'
  };

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MyDetailPage');
  }


  openNavDetailsPage(item) {
    switch (item){
      // 修改手机号
      case 'editTelFirst':this.navCtrl.push('editTelFirst');break;
      // 修改头像
      case 'editProfile':this.navCtrl.push('editProfile');break;
      // 修改登录密码
      case 'editPwd':this.navCtrl.push('editPwd');break;
      default:console.log('-');
    }
  }


}



