let SERVER:IConfig;
let loadConfig=()=>{
    let xhr = new XMLHttpRequest();  
    //同步获取
    xhr.open("GET","assets/config.json",false);  
    xhr.send();  
    let conf=JSON.parse(xhr.responseText);
    SERVER=conf.SERVER; 
}
loadConfig();
export {
    SERVER
}

interface IConfig{
    MAIN:string;
}


