import { NgModule } from '@angular/core';
import { SearchComponent } from './search/search';
import {IonicModule} from "ionic-angular";
import { CustomerProfileComponent } from './customer-profile/customer-profile';
import { EmptyReminderComponent } from './empty-reminder/empty-reminder';
import { SearchLineComponent } from './search-line/search-line';
import {PipesModule} from "../pipes/pipes.module";
@NgModule({
	declarations: [SearchComponent,
    CustomerProfileComponent,
    EmptyReminderComponent,
    SearchLineComponent],
	imports: [IonicModule,PipesModule],
	exports: [SearchComponent,
    CustomerProfileComponent,
    EmptyReminderComponent,
    SearchLineComponent]
})
export class ComponentsModule {}



