import { Component } from '@angular/core';
import { NavController, NavParams, ToastController} from 'ionic-angular';

/**
 * Generated class for the LoanRecordPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage({
//   name:'loan-record'
// })
@Component({
  selector: 'page-loan-record',
  templateUrl: 'loan-record.html',
})
export class LoanRecordPage {
  loadRecordList=[
    {date:'2017-09-13 13:40',statusText:'申请中',statusKey:'sqz'},
    {date:'2017-09-13 13:40',statusText:'已结清',statusKey:'sqz'},
    {date:'2017-09-13 13:40',statusText:'申请中',statusKey:'sqz'},
    {date:'2017-09-13 13:40',statusText:'已结清',statusKey:'sqz'},
  ];
  constructor(public navCtrl: NavController,
              public toastCtrl: ToastController,
              public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoanRecordPage');
  }
  handleInput(val){
    console.log('val',val);
    if(Math.random()>0.5){
      console.log('val----',val);
      this.loadRecordList = [];
    }else{
      this.loadRecordList=[
        {date:'2017-09-13 13:40',statusText:'申请中',statusKey:'sqz'},
        {date:'2017-09-13 13:40',statusText:'已结清',statusKey:'sqz'},
        {date:'2017-09-13 13:40',statusText:'申请中',statusKey:'sqz'},
        {date:'2017-09-13 13:40',statusText:'已结清',statusKey:'sqz'},
        {date:'2017-09-13 13:40',statusText:'已结清',statusKey:'sqz'},
        {date:'2017-09-13 13:40',statusText:'已结清',statusKey:'sqz'},
        {date:'2017-09-13 13:40',statusText:'已结清',statusKey:'sqz'},
      ];
    }
  }
  doInfinite(infiniteScroll) {
    console.log('Begin async operation');

    setTimeout(() => {
      for (let i = 0; i < 30; i++) {
        this.loadRecordList.push( {date:'2017-09-13 13:40',statusText:'申请中'+i,statusKey:'sqz'});
      }

      console.log('Async operation has ended');
      infiniteScroll.complete();
    }, 500);
  }



}



